<template>
  <div>
    <div v-if="menubar" :id="`${id}Menubar`" class="JS9Menubar" :data-width="width"></div>
    <div v-if="toolbar" :id="`${id}Toolbar`" class="JS9Toolbar" :data-width="width"></div>
    <div v-if="main" :id="`${id}`" class="JS9" :data-width="width" :data-height="height"></div>
    <div v-if="colorbar" style="margin-top: 2px;"></div>
    <div v-if="colorbar" :id="`${id}Colorbar`" class="JS9Colorbar" :data-width="width"></div>
  </div>
</template>
<script>
export default {
  props: {
    id: String,
    source: String,
    width: String,
    height: String,
    menubar: Boolean,
    toolbar: Boolean,
    main: Boolean,
    colorbar: Boolean,
    scale: String,
    colormap: String,
    zoom: [String, Number]
  },
  name: 'VFits',
  data: function () {
    return {
      scales: ['linear', 'log', 'histeq', 'power', 'sqrt', 'squared', 'asinh', 'sinhh'],
      colormaps: ['viridis', 'magma', 'inferno', 'plasma', 'grey', 'red', 'green', 'blue', 'heat', 'cool', 'i8', 'aips0', 'sls', 'a', 'b', 'bb', 'he', 'hsv', 'rainbow', 'standard', 'staircase', 'color'],
      zooms: ['in', 'out', 'toFit', 1 / 32, 1 / 16, 1 / 8, 1 / 4, 1 / 2, 1, 2, 4, 8, 16, 32]

    }
  },
  beforeCreate: function () {
    /* eslint-disable */
    console.log("beforeCreated")
    /* eslint-enable */
  },
  created: function () {
    /* eslint-disable */
    JS9.init(this.id)
    JS9.Preload(this.source, {display: this.id, scale: "sqrt", colormap: "bb", zoom: this._zoom} )
    console.log("created")
    //JS9.Preload(this.source, {display: this.id, scale: "sqrt", colormap: "bb", zoom: this._zoom} )
    /* eslint-enable */
  },
  beforeMount: function () {
    /* eslint-disable */
    console.log("beforeMount")
    //JS9.Preload(this.source, {display: this.id, scale: "sqrt", colormap: "bb", zoom: this._zoom} )
    /* eslint-enable */
  },
  mounted: function () {
    /* eslint-disable */
    
    
    console.log("mounted")
    /* eslint-enable */
  },
  beforeUpdate: function () {
    /* eslint-disable */
    console.log("beforeUpdadfsvdxte")
    /* eslint-enable */
  },
  updated: function () {
    /* eslint-disable */
    console.log("updaatsaded")
    /* eslint-enable */
  },
  beforeDestroy: function () {
    /* eslint-disable */
    console.log("beforeDestroy")
    /* eslint-enable */
  },
  destroyed: function () {
    /* eslint-disable */
    console.log("Dedadssddasdatroydased")
    JS9.RefreshImage(this.source, { display: this.id } )
    JS9.init()
    /* eslint-enable */
  },
  computed: {
    _zoom () {
      return this.zooms.includes(this.zoom) ? this.zoom : 1
    },
    _scale () {
      return this.scales.includes(this.scale) ? this.scale : 'linear'
    },
    _colormap () {
      return this.colormaps.includes(this.colormap) ? this.colormap : 'grey'
    }
  },
  watch: {
    /* eslint-disable */
    zoom (val) {
      JS9.SetZoom(this._zoom)
    },
    watch (val) {
      JS9.SetScale(this._scale)
    }
    /* eslint-enable */
  }
}
</script>
